
import slide from 'components/slide/index'
import http from 'js/http'
import Qrcode from 'vux/src/components/qrcode/index.vue'

import Clipboard from 'clipboard';
import XDialog from 'vux/src/components/x-dialog/index.vue'

import {base} from 'js/constant'

export default {
name: "index",
components: {
slide,
Qrcode,
XDialog
},
data(){
return{
rcmdCode:'',
isAddCode:true,
show:false,
code:'',
btnLoading: false
}
},
methods:{
getRcmdCode(){
http.post('/user/recommend-code', {}, (res) => {
if (res.code !== 0) {
this.$vux.toast.show(res.msg)
return
}
this.rcmdCode = res.content.code
this.isAddCode = res.content.reCode
})
},
submitCode() {
if (!this.code) {
this.$vux.toast.show('请输入或粘贴介绍人邀请码')
return
}
this.btnLoading = true
http.post('/user/add-recommend', {
re_code:this.code
}, (res) => {
this.btnLoading = false
if (res.code !== 0) {
this.$vux.toast.show(res.msg)
}else {
this.$vux.toast.show({
text: res.msg,
type: 'success'
})
this.show = false
}
this.code = ''
this.isAddCode = true

})
}
},
computed:{
qrcodeData(){
let url = window.location.href
let aurl = url.split('#')
return `${aurl[0]}#/login?re_code=${this.rcmdCode}`
}
},
created(){
this.getRcmdCode()
},
mounted(){
const clipboard = new Clipboard('#copy');

clipboard.on('success', (e) => {
this.$vux.toast.show('复制成功')
});

clipboard.on('error', (e) => {
this.$vux.toast.show('复制失败，你可以选择手动复制')
});
},
}



